h1{font-family:"Raleway";font-weight:700 !important;color:#fff;font-size:3.5rem !important}p{color:#fff;font-weight:400}a.social{margin:0 1rem;transition:-webkit-transform 250ms;transition:transform 250ms;transition:transform 250ms, -webkit-transform 250ms;display:inline-block;color:#fff;text-decoration:none}a.social:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px);color:#b9a3ff}.social-links{margin-top:2rem}.bg-photo{background-repeat:no-repeat;background-size:contain;background:linear-gradient(0deg, rgba(33, 37, 41, 0.6), rgba(33, 37, 41, 0.6)),url(/static/media/computer.32c16a65.jpg);background-size:cover}
.navigation{position:absolute;top:0;left:0}.navigation .nav-image{height:8rem;width:auto;padding:1rem}
